@import url(https://fonts.googleapis.com/css?family=Titillium+Web:200,200i,300,300i,400,400i,600,600i,700,700i,900&subset=latin-ext);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;600;700&display=swap);
body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "Titillium Web", sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.menu {
  font-family: 'Titillium Web', sans-serif;
  font-weight: bold;
}
#carousel h1 {
  font-family: 'Open Sans', sans-serif;
  font-weight: bold;
}

@media (max-width: 850px) {
  #carousel img {
    height: 400px;
    object-fit: cover;
  }
}

.body-inicio img {
  width: 600px;
}


